.navvv {
  margin-top: 30px;
}

.navv {
  display: inline-flex;
  margin: 0px 12px;
  color: #ffff;
  font-weight: 500;
  font-size: 20px;
  font-family: "Gill Sans", "Gill Sans MT", Calibri, "Trebuchet MS", sans-serif;
}

.logo {
  color: white;
  font-family: "Monoton", cursive;
  font-size: 3em;
  background-color: none;
}

.anime {
  border: 0px solid white;
  border-radius: 5px;
  width: 18px;
  height: 15px;
  background-color: white;
  position: relative;
  -webkit-animation-name: logoo;
          animation-name: logoo;
  -webkit-animation-duration: 10s;
          animation-duration: 10s;
  -webkit-animation-iteration-count: 50;
          animation-iteration-count: 50;
  -webkit-animation-delay: 2s;
          animation-delay: 2s;
}

@-webkit-keyframes logoo {
  0% {
    background-color: white;
    left: 0px;
    top: 0px;
  }
  25% {
    background-color: green;
    left: 480px;
    bottom: 0px;
  }
  50% {
    background-color: green;
    left: 1px;
    top: -40px;
  }
  75% {
    background-color: navy;
    left: 0px;
    top: 0px;
  }
  100% {
    background-color: white;
    left: 0px;
    top: -15px;
  }
}

@keyframes logoo {
  0% {
    background-color: white;
    left: 0px;
    top: 0px;
  }
  25% {
    background-color: green;
    left: 480px;
    bottom: 0px;
  }
  50% {
    background-color: green;
    left: 1px;
    top: -40px;
  }
  75% {
    background-color: navy;
    left: 0px;
    top: 0px;
  }
  100% {
    background-color: white;
    left: 0px;
    top: -15px;
  }
}

.active {
  border-bottom: 2px solid white;
}

.activee {
  border-bottom: 3px solid red;
}

.a:hover {
  padding-bottom: 5px;
  border-bottom: 2px solid #ffff;
}

.b:hover {
